
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin :0px;
}

@charset "UTF-8";
.main, .mainElse {
  width: 582px;
  height: 700px;
  margin: 0 auto;
  margin-left: 100px;
  position: relative;
}
.main .top, .main .bottom, .mainElse .top, .mainElse .bottom {
    width: 100%;
    height: 50px;
}
.main .top ul, .main .bottom ul, .mainElse .top ul, .mainElse .bottom ul {
      width: 100%;
      height: 50px;
      padding-left: 0px;
      margin: 0px 0px 0px -25px;
}
.main .top ul li, .main .bottom ul li, .mainElse .top ul li, .mainElse .bottom ul li {
        display: inline-block;
        font-size: 22px;
        line-height: 50px;
        float: left;
        height: 50px;
        width: 50px;
}
.main .chess, .mainElse .chess {
    margin: 25px 0px;
    background: url(../../static/img/chessBg.914bb57.jpg);
    background-size: 400px 450px;
    background-repeat: no-repeat;
    width: 400px;
    height: 450px;
}
.mainElse span {
  display: inline-block;
  width: 50%;
  height: 100%;
  font-size: 40px;
  color: white;
  line-height: 700px;
  text-align: center;
}
.chess span {
  width: 50px;
  height: 50px;
  position: absolute;
  line-height: 50px;
  /*transition-duration: 2s;*/
}
.chess .black {
  /*display: block;*/
  background-size: 50px 50px;
  width: 37px;
  height: 37px;
  border-radius: 40px;
  border: #4e3817 double 5px;
  background-color: #c0956a;
  margin: auto;
  text-align: center;
  font: 30px "\6977\4F53";
  line-height: 40px;
  text-shadow: 0px 0px 2px white;
  -webkit-box-shadow: 1px 1px 1px black;
          box-shadow: 1px 1px 1px black;
  /*position: absolute;*/
}
.chess .red {
  background-size: 50px 50px;
  width: 37px;
  height: 37px;
  border-radius: 40px;
  border: #4e3817 double 5px;
  background-color: #c0956a;
  margin: auto;
  text-align: center;
  font: 30px "\6977\4F53";
  line-height: 40px;
  text-shadow: 0px 0px 2px white;
  -webkit-box-shadow: 1px 1px 1px black;
          box-shadow: 1px 1px 1px black;
  color: #900b0b;
  border-color: #900b0b;
}
.select, .back {
  width: 100%;
  height: 50px;
}
.select ul, .back ul {
    width: 100%;
    height: 50px;
    padding-left: 0px;
    margin: 0px 0px 0px -25px;
}
.select ul li, .back ul li {
      display: inline-block;
      font-size: 22px;
      line-height: 50px;
      float: left;
      margin-left: 3px;
      height: 47px;
      width: 47px;
      background-color: aqua;
}
.select ul li:hover, .back ul li:hover {
        background-color: aquamarine;
}
.select .notClick, .back .notClick {
    background-color: gray;
}
.select .notClick:hover, .back .notClick:hover {
      background-color: gray;
}
.back {
  margin-top: -100px;
}
.message {
  margin-left: -200px;
}
.animated-move {
  -webkit-transition: -webkit-transform 2s;
  transition: -webkit-transform 2s;
  transition: transform 2s;
  transition: transform 2s, -webkit-transform 2s;
}
.animated-enter-active {
  -webkit-animation: fade 2s;
          animation: fade 2s;
}
.animated-leave-active {
  animation: fade 2s reverse;
}
@-webkit-keyframes fade {
from {
    opacity: 0;
}
to {
    opacity: 1;
}
}
@keyframes fade {
from {
    opacity: 0;
}
to {
    opacity: 1;
}
}
